<template>
	<div>
		<video
			class="video"
			controls="controls"
			ref="video"
			playsinline
			:src="currentItem && currentItem.videoUrl"
			:poster="currentItem && currentItem.vodrow.coverpic"
		></video>
		<cube-slide
			class="swiper"
			ref="slide"
			direction="direction"
			:autoPlay="false"
			:loop="false"
			:options="options"
			:data="rows"
			@change="changePage"
		>
			<cube-slide-item
				class="swiperItem"
				v-for="(item, index) in rows"
				:key="index"
				@click.native="clickHandler(item, index)"
			>
				<div class="content">
					<img :src="item.vodrow.coverpic">
				</div>
				<div class="mask" v-if="isShowMask">
					<div>@{{item.user.nickname}}</div>
					<div>{{item.vodrow.title}}</div>
					<div>{{item.vodrow.updatetime}}</div>
				</div>
			</cube-slide-item>
		</cube-slide>
	</div>
</template>

<script>
import config from "../js/config.js";
export default {
  data() {
    return {
      rows: [],
      current: 0,
      isShowMask: true,
      options: {
        scrollY: true
      }
    };
  },
  computed: {
    currentItem() {
      return this.rows.length > 0 ? this.rows[this.current] : null;
    }
  },
  created() {
    console.log("created banana");
    this.loadData(true);
  },
  mounted() {
    //   document.body.appendChild(this.$refs.swiperContainer)
  },
  methods: {
    loadData(isFirst) {
      this.$axios
        .request({
          url: config.origin + "/minivod/reqlist",
          params: config.query(),
          success: res => {
            console.log("success", res);
            if (res.data.retcode == 0) {
            }
          },
          fail(res) {
            console.log("fail", res);
          }
        })
        .then(res => {
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            var rows = [];
            res.data.data.rows.map(item => {
              if (item.vodrow && item.vodrow.vodid) {
                item.videoUrl = "";
                rows.push(item);
              }
            });
            this.rows = isFirst ? rows : this.rows.concat(rows);
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch(res => {
          weui.topTips(res.message || "fail");
        });
    },
    clickHandler(item, index) {
      this.$refs.video.play();
      this.changePage(index);
    },
    changePage(current) {
      const item = this.rows[current];
      const vodid = item.vodrow.vodid;
      console.log("swiperChange", current, item);
      if (document) {
        document.title = item.vodrow.title;
      }
      this.current = current;
      this.$axios
        .request({
          url: config.origin + "/minivod/reqplay/" + vodid,
          params: config.query()
        })
        .then(res => {
          console.log("success", res);
          console.log("success", res);
          var videoUrl = res.data.data.httpurl || res.data.data.httpurl_preview;
          if (videoUrl) {
            console.log(videoUrl);
            this.rows[current].videoUrl = videoUrl;
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch(res => {
          weui.topTips(res.message || "fail");
        });
      if (current == this.rows.length - 1) {
        this.loadData();
      }
      for (var i = 0; i < this.rows.length; i++) {
        let video = this.$refs["video" + i];
        if (Array.isArray(video)) {
          video = video[0];
        }
        if (i == this.current) {
          setTimeout(() => {
            video && video.play && video.play();
          }, 500);
        } else {
          video && video.pause && video.pause();
        }
      }
    }
  }
};
</script>

